import CustomImage from '../Image/Image';
import './TextDisplay.css';

type ImageTextProps = {
  text: string,
  source: string,
  imageNumber: number,
  caption: string,
  alt: string
}

type DisplayFullProps = {
    text: string
  }

export const TextDisplayImageRight = ({text, source, imageNumber, caption, alt}: ImageTextProps) =>
    <div className='text-image-box'>
        <p className='written-text'>{text}</p>
        <CustomImage source={source} imageNumber={imageNumber} caption={caption} alt={alt}/>    
    </div>

export const TextDisplayImageLeft = ({text, source, imageNumber, caption, alt}: ImageTextProps) =>
    <div className='text-image-box'>
        <CustomImage source={source} imageNumber={imageNumber} caption={caption} alt={alt}/>    
        <p className='written-text'>{text}</p>
    </div>

export const TextDisplayFull = ({text}:DisplayFullProps) =>
  <div className='text-image-box'>
    <p className='written-text'>{text}</p>
  </div>