import { BrowserRouter, Routes, Route } from 'react-router-dom';

//pages
import HomePage from './views/HomePage/Home';
import Blogpost from './views/Blogpost/Blogpost';
import ContactPage from './views/Contact/ContactPage';
import NoPage from './views/NoPage/NoPage';

//blogpost pages
import Nov29 from './blogposts/Nov29';
import Nov11Summit from './blogposts/Nov11';
import Oct1 from './blogposts/Oct1';
import Oct11 from './blogposts/Oct11';
import Oct13 from './blogposts/Oct13';
import Oct18 from './blogposts/Oct18';
import Oct20 from './blogposts/Oct20';
import Oct23 from './blogposts/Oct23';
import Oct8 from './blogposts/Oct8';
import Oct9 from './blogposts/Oct9';
import Sep15 from './blogposts/Sep15';
import Sep18 from './blogposts/Sep18';
import Sep20 from './blogposts/Sep20';
import Sep22 from './blogposts/Sep22';
import Sep27 from './blogposts/Sep27';
import Sep29 from './blogposts/Sep29';
import Sep6 from './blogposts/Sep6';
import Stitchonary from './projects/Stitchonary';
import Portfolio from './projects/PortfolioProcess';
import Sep24 from './blogposts/Sep24';

//project

function Router() {
    return (
        <BrowserRouter>
        <Routes>
          {/* Base */}
          <Route index element={<HomePage />}/>
          <Route path="/ntrejo/" element={<HomePage />}/>
          <Route path="/ntrejo/all-blogposts" element={<Blogpost />}/>
          <Route path="/ntrejo/contact" element={<ContactPage />}/>

          {/* Blogposts */}
          <Route path="/ntrejo/blogposts/design-rules-for-powerpoint" element={<Sep6 />}/>
          <Route path="/ntrejo/blogposts/christmas-color-theory" element={<Sep15 />}/>
          <Route path="/ntrejo/blogposts/clicker-heroes-color-analysis" element={<Sep18 />}/>
          <Route path="/ntrejo/blogposts/resumes-and-text" element={<Sep20 />}/>
          <Route path="/ntrejo/blogposts/color-gradients" element={<Sep22 />}/>
          <Route path="/ntrejo/blogposts/age-and-the-color-blue" element={<Sep24 />}/>
          <Route path="/ntrejo/blogposts/nav-bars-as-headers" element={<Sep27 />}/>
          <Route path="/ntrejo/blogposts/honesty-not-kindness" element={<Sep29 />}/>
          <Route path="/ntrejo/blogposts/deciding-design" element={<Oct1 />}/>
          <Route path="/ntrejo/blogposts/am-i-next" element={<Oct8 />}/>
          <Route path="/ntrejo/blogposts/powerpoint-structure" element={<Oct9 />}/>
          <Route path="/ntrejo/blogposts/prototyping" element={<Oct11 />}/>
          <Route path="/ntrejo/blogposts/analyzing-portfolios" element={<Oct13 />}/>
          <Route path="/ntrejo/blogposts/on-user-testing" element={<Oct18 />}/>
          <Route path="/ntrejo/blogposts/on-the-debrief" element={<Oct20 />}/>
          <Route path="/ntrejo/blogposts/vscode-acessibility" element={<Oct23 />}/>
          <Route path="/ntrejo/blogposts/interview-prep" element={<Nov29 />}/>
          <Route path="/ntrejo/blogposts/ui-summit" element={<Nov11Summit />}/>

          {/* Project */}
          <Route path="/ntrejo/projects/stitchonary" element={<Stitchonary />}/>
          <Route path="/ntrejo/projects/portfolio" element={<Portfolio />}/>

          {/* Other */}
          <Route path="*" element={<NoPage />} />
        </Routes>
        </BrowserRouter>    
    );
}

export default Router