import init from './images/10-20-taskprioritization.jpg'
import NavBar from "../components/NavBar/NavBar";
import { TextDisplayImageRight, TextDisplayFull } from "../components/TextDisplays/TextDisplay";

export default function Oct20() {
    return (
        <div className="page">
            <NavBar value={""} />
            <div className='allBlogpostsPage'>
                <div className="allPostsTitleSection">
                    <br></br>
                    <h1 className='postTitle'>On The Debrief</h1>
                    <div className='postTypeDate'>
                        <h2>Free Form</h2>
                        <h2>Oct 20, 2023</h2>
                    </div>
                    <br></br>
                </div>

                <br></br>
                <br></br>
                <div className="introOutro">
                    <TextDisplayImageRight text={"I have been hearing a lot of difference between prioritisation of tasks. For one, it's do what has the most amount of value for least amount of work. For this, it is choosing the most serious problems and working down. Both make sense, but it's odd as I would have expected the development to have the most, find the most needed item first. That was interesting enough, and perhaps for UI doing the most serious isn't as problematic if it goes long. Regardless, the breaking down of tasks across the two remain the same"} source={init} imageNumber={1} caption={"Task Prioritization Matrix"} alt={"Task Prioritization Matrix"} />

                </div>


                <div className="citation-section">
                    <h3 className="Text-Headers"> Inspired By</h3>
                    <ul className="citationList">
                        <li className="citation"> Don't Make Me Think pg 137-141
                        </li>
                    </ul>
                </div>


                <br></br>
                <br></br>
            </div>
        </div>)
}
