import init from './images/oct18-comic.png'
import NavBar from "../components/NavBar/NavBar";
import { TextDisplayImageRight, TextDisplayFull } from "../components/TextDisplays/TextDisplay";

export default function Oct18() {
    return (
<div className="page">
            <NavBar value={""} />
            <div className='allBlogpostsPage'>
                <div className="allPostsTitleSection">
                    <br></br>
                    <h1 className='postTitle'>On User Testing</h1>
                    <div className='postTypeDate'>
                        <h2>Free Form</h2>
                        <h2>Oct 18, 2023</h2>
                    </div>
                    <br></br>
                </div>

                <br></br>
                <br></br>
                <div className="introOutro">
                    <TextDisplayImageRight text={"The first comic in Don't Make Me Think hits a little bit too close to home. In Design Studio at the moment, we are designing the front end of a website and a lot of the conversations we get into sound similar to that pictured above. We are trying to get user testing done, moreso to move on as well as have something usable and not built directly for us. We have had more focus group testing and not really user testing as we describe in the abstract what should and should not be happening. Maybe it would be useful, since we are having issues directly having customer interaction, to do the do-it-yourself testing. In it, it's a loose recruitment of people and while it is not exact, it's better than nothing."} source={init} imageNumber={1} caption={"User Testing Comic"} alt={"User Testing Comic"} />

                </div>


                <br></br>
                <div className="citation-section">
                    <h3 className="Text-Headers"> Inspired By</h3>
                    <ul className="citationList">
                        <li className="citation"> Don't Make Me Think Chapters 8 and 9
                        </li>
                        <li className="citation"> The Design of Everyday Things Chapter 5
                        </li>
                    </ul>
                </div>


                <br></br>
                <br></br>
            </div>
        </div>)
}
