import init from './images/11-29-interviewer.jpg'
import NavBar from "../components/NavBar/NavBar";
import { TextDisplayImageRight, TextDisplayFull } from "../components/TextDisplays/TextDisplay";

export default function Nov29() {
  return (
    <div className="page">
      <NavBar value={""} />
      <div className='allBlogpostsPage'>
        <div className="allPostsTitleSection">
          <br></br>
          <h1 className='postTitle'>Interview Prep</h1>
          <div className='postTypeDate'>
            <h2>Compare and Contrast</h2>
            <h2>Nov 29, 2023</h2>
          </div>
          <br></br>
        </div>

        <br></br>
        <br></br>
        <div className="introOutro">
          <TextDisplayFull text={'Due to some upcoming circumstances, I decided it would be best to prep a bit over UI/UX interviews and how they differ from the CS interviews that I have been a part of. To do so I found two different videos talking about how to ace interviews and used those as the basis for this post. There was significant overlap with the interviews that I&rsquo;ve been through, that&rsquo;s to be expected as it is an interview. There are still generic questions such as: tell me about yourself, what are your strengths and weaknesses, and why do you want to work here. The structure is also pretty similar with stages of general and then technical questions with the possibility of a technical exam, although that&rsquo;s now design and not programming. Out of it all, the most important thing I got was to think what the interviewer want to hear. A list of tips is below as well as the citations used.'} />
          <TextDisplayImageRight text={"1. Think about what the interviewer is trying to learn.\n 2.The interview is for you. Don''t get caught in the details.\n 3. Focus on what you can give to the company, not what they can do for you. \n 4.Overcoming challenges easily is not realistic, describe actual conflict.\n 5.For project walkthrough focus on your story in it."} source={init} imageNumber={1} caption={"Examples of text over images"} alt={"Images with text overlayed successfuly."} />

        </div>


        <br></br>
        <div className="citation-section">
          <h3 className="Text-Headers"> Inspired By</h3>
          <ul className="citationList">
            <li className="citation"> https://www.youtube.com/watch?v=ZtN3yHIWuRY
            </li>
            <li className="citation"> https://www.youtube.com/watch?v=vu3xcQm7cEo
            </li>
          </ul>
        </div>


        <br></br>
        <br></br>
      </div>
    </div>
  )
}
