import init from './images/10-9textoverimage.jpg'
import NavBar from "../components/NavBar/NavBar";
import { TextDisplayImageRight, TextDisplayFull } from "../components/TextDisplays/TextDisplay";

export default function Oct9() {
    return (
        <div className="page">
            <NavBar value={""} />
            <div className='allBlogpostsPage'>
                <div className="allPostsTitleSection">
                    <br></br>
                    <h1 className='postTitle'>The Powerpoint Structure</h1>
                    <div className='postTypeDate'>
                        <h2>Free Form</h2>
                        <h2>Oct 9, 2023</h2>
                    </div>
                    <br></br>
                </div>

                <br></br>
                <br></br>
                <div className="introOutro">
                    <TextDisplayImageRight text={"One of the hardest part of PowerPoints, is figuring out how to place text over an image or two different colors. It's a balancing act of getting enough contrast that it is readable, but making sure the image is still visible as well. The text provides a good solution, having a section with low opacity the text lies upon. It gives the text a single color to contrast against, making it easier to work with. This is different from what I getneraly do which is darken the whole image and place white text to show distance."} source={init} imageNumber={1} caption={"Examples of text over images"} alt={"Images with text overlayed successfuly."} />

                </div>


                <br></br>
                <div className="citation-section">
                    <h3 className="Text-Headers"> Inspired By</h3>
                    <ul className="citationList">
                        <li className="citation"> Presentation Zen Chapters 2 and 4
                        </li>
                    </ul>
                </div>


                <br></br>
                <br></br>
            </div>
        </div>)
}
