import init from './images/10-13portfolios.png'
import NavBar from "../components/NavBar/NavBar";
import { TextDisplayImageRight, TextDisplayFull } from "../components/TextDisplays/TextDisplay";
export default function Oct13() {
    return (
        <div className="page">
            <NavBar value={""} />
            <div className='allBlogpostsPage'>
                <div className="allPostsTitleSection">
                    <br></br>
                    <h1 className='postTitle'>Analyzing Portfolios</h1>
                    <div className='postTypeDate'>
                        <h2>Free Form</h2>
                        <h2>Oct 13, 2023</h2>
                    </div>
                    <br></br>
                </div>

                <br></br>
                <br></br>
                <div className="introOutro">
                    <TextDisplayImageRight text={"Looking though the portfolios, there was one thing clear. I need to be thinking about what to add and show and in what order. I particularly like what they said about visitors. They care about seeing the process and work because that's what the website about. An easy solution would be pushing blogposts further down, something I hadn't considered yet. In the future I'll think more about the user and what they wat as well as the design of the blogpost."} source={init} imageNumber={1} caption={"Stock portfolio Visual"} alt={"Portfolio visual"} />

                </div>


                <br></br>
                <div className="citation-section">
                    <h3 className="Text-Headers"> Inspired By</h3>
                    <ul className="citationList">
                        <li className="citation"> https://blog.uxfol.io/ux-portfolio-examples/
                        </li>
                    </ul>
                </div>


                <br></br>
                <br></br>
            </div>
        </div>)
}
