import NoPage from "../views/NoPage/NoPage";
import { TextDisplayImageRight, TextDisplayFull } from "../components/TextDisplays/TextDisplay";
import NavBar from "../components/NavBar/NavBar";
import init from './images/9-27-poormockup.png'

export default function Sep27() {
    return (
        <div className="page">
            <NavBar value={""} />
            <div className='allBlogpostsPage'>
                <div className="allPostsTitleSection">
                    <br></br>
                    <h1 className='postTitle'>Navigation Bars as Headers</h1>
                    <div className='postTypeDate'>
                        <h2>Sketch Inspiration</h2>
                        <h2>Sept 27, 2023</h2>
                    </div>
                    <br></br>
                </div>

                <br></br>
                <br></br>
                <div className="introOutro">
                    <TextDisplayImageRight text={"Header bars have been on my brain a lot recently. Mostly due to Design Studio, but I have spent an unreasonable amount of time thinking what should and shouldn't be on it. In theory it's simple: the logo, navigation (bar or menu). Though according to the book, focus on minimizing unused space on screen s to help with the content flow in the page. Below is a rough mockup that doesn't use that concept."} source={init} imageNumber={1} caption={"Feedback"} alt={"Nav Bar Mockup"} />

                    <TextDisplayFull text={"In this image, the main issue is the redundancy of the color bar as well as the blank space at the end of the logo. It takes up real estate on the screen as well as makes it feel unbalanced.  "} />
                </div>


                <br></br>
                <div className="citation-section">
                    <h3 className="Text-Headers"> Inspired By</h3>
                    <ul className="citationList">
                        <li className="citation">Don't Make Me Think Chapter 7 </li>
                    </ul>
                </div>


                <br></br>
                <br></br>
            </div>
        </div>
    )
}
