import init from './images/rice-scoring-formula.png'
import NavBar from "../components/NavBar/NavBar";
import { TextDisplayImageRight, TextDisplayFull } from "../components/TextDisplays/TextDisplay";
import { IonAccordionGroup, IonAccordion, IonItem } from '@ionic/react';

export default function Nov11Summit() {
  return (
    <div className="page">
      <NavBar value={""} />
      <div className='allBlogpostsPage'>
        <div className="allPostsTitleSection">
          <br></br>
          <h1 className='postTitle'>A Reflection on
            the 2023 Summit Negotiations</h1>
          <div className='postTypeDate'>
            <h2>Free Form</h2>
            <h2>Nov 11, 2023</h2>
          </div>
          <br></br>
        </div>

        <br></br>
        <br></br>
        <div className="introOutro">
          <TextDisplayFull text={'This fall, I was honored to be a part of the 2023 UIUN Summit Negotiations, an accessibility workshop hosted to get younger designers thinking about accessibility through someone else’s eyes. Over three sessions across the workweek, each delegate presented the need’s of their group in relation to the Sarpy County website and then a negotiation took place to determine the top fifteen requirements for the website. Each group was required to have at least one requirement that would help their group on the final decision.'} />

          <TextDisplayFull text={'At the conference, I was selected to represent photosensitivity, also known as photophobia. This is a condition that causes people discomfort or pain when adjusting to levels of light or looking at something bright. This also extends to cold versus warm light as the difference between two equally bright sources may also be disorienting. People with photosensitivity minimize the amount of light changes they’re exposed to, opting for sunglasses as well as a dark mode on websites to reduce backlighting. In the sense of UI design, the best thing for this group is dark mode with lower contrast as well as accessible to screen readers if they decide to forgo looking at the screen entirety.'} />

          <TextDisplayFull text={'Admittedly my first thought when I heard about the conference was that it was absolutely never going to work. There are tons of people with different needs, frequently in competition, and having baby boomers, Gen Z, and children on the roster didn’t fill me with confidence. With that in mind, I settled down to listen to the presentations of each group and determine who shared similar interests to get onto my side and work with.'} />

          <TextDisplayFull text={'When negotiations and requirement setting began, I didn’t know where to go. I had very concrete goals of getting a dark mode on the list but beyond that, I wanted to make it more screen reader accessible. In doing so, I found an ally with the blindness representative as we both wanted to increase screen reader accessibility. After an initial conflict over color schemes, I soon quickly paired up with the color-blind associates as they argued for color-agnostic icons and text dictating the meanings. '} />

          <TextDisplayFull text={'After ending negotiations, my group had the following on the requests board. 1) Customizable color options with \n a. changeable contrast between text/background \n b. light/dark mode \bc.colorblind mode. \n 2) Buttons should have clear, large activation areas, simple icons/text in addition to different colors, with effective screen reader text \n 3) Consistent conveying of information (action item messaging, color-agnostic \n 4)Unique alt-text for every non-decorative image \n'} />

          <TextDisplayFull text={'Out of the conference as a whole, I learned three things.'} />

          <div className="postContent">
            <IonAccordionGroup multiple={true}>
              <IonAccordion value="first">
                <IonItem slot="header" color="light">
                  <h3 className="Text-Headers"> 1. Easy to Generalize, Hard to Deliver </h3>
                </IonItem>
                <div className="ion-padding" slot="content">
                  <TextDisplayFull text={"When discussing and negotiating, a majority of sweeping claims were made and it became more of a matter to try and include each group via phrasing. The best example of this was regarding to colors. There were three different camps initially: color-blind friendly, dark mode, and low contrast. Each of these was given different priorities and labeled as separate requirements. However, in negotiations, they all got combined into one mass statement. While this is easier to work around, it’s going to be harder to execute and became a sort of slippery slope. It became a reverse to how agile processes work, and in doing so would surely cause problems should we continue and implement the changes. "} />
                </div>

              </IonAccordion>
              <IonAccordion value="second">
                <IonItem slot="header" color="light">
                  <h3 className="Text-Headers"> 2. Reach vs Impact: Who gets what they want?</h3>
                </IonItem>
                <div className="ion-padding" slot="content">
                  <TextDisplayFull text={"A common negotiation tactic was stating how many people would be affected by a requirement. In doing so, it gave more support to what a person was arguing for, but also raised a problem. How do we decide what’s more important? A low-impact feature that reaches everyone or a feature that a specific group needs for site functionality. In my negotiations, that resulted in the media section arguing between unique alt-text for images versus color-blind accessible images and videos. While the colorblind option reached more people, it also had a lower quality of life benefit in comparison to alt text being necessary for screen readers. In the end we went with alt text above colorblind option, but it led to the question of how to prioritize features."} />
                  <TextDisplayImageRight text={"A common way of balancing these types of decisions is the RICE scoring method. RICE is a prioritization method commonly used by managers to determine what features are added in what order. R talks about reach, the number of people affected. I stands for impact, the importance of the feature. C is for confidence, the confidence that this feature will work as expected. Finally, E stands for the effort necessary to make the change happen. The calculation for each is below."} source={init} imageNumber={1} caption={"RICE score calculations"} alt={"Visual of the formula for RICE scores"} />
                  <TextDisplayFull text={"According to its importance, each category would have a coefficient that would be used to weight them. While it could be seen as cold to use this to determine priorities for people, it is at least a starting off point to consider in design and feature planning. "} />
                </div>
              </IonAccordion>
              <IonAccordion value="third">
                <IonItem slot="header" color="light">
                  <h3 className="Text-Headers">3. Diversity of Needs, Adjustments for Inclusivity</h3>
                </IonItem>
                <div className="ion-padding" slot="content">
                  <TextDisplayFull text={"One of the things that took me off guard in the negotiations was how many sections there were for groups. There was dynamically loaded content, copy, navigation, forms, ext. that I had never considered relevant to the idea of inclusivity. A lot of it didn’t relate to my group and I had assumed that it would be similar for others. I was quickly mistaken, with copy and dynamically loaded content have fierce arguments on both sides over load times and public access, things that weren’t even possible to account for by UI designers. Overall, the burden of that is in development, meaning that we, as developers, also need to keep in mind how our code can impact the usability of a sight. An inefficient API call and parsing may result to a long load time that is difficult for Gen Z to feel engage with or minimizing the amount of data used may make it more friendly to rural users that have less of access to WiFi and data. Inclusivity doesn’t end at prototyping but extends the whole process."} />
                </div>
              </IonAccordion>
            </IonAccordionGroup>
          </div>
        </div>
        <br></br>
        <br></br>
        <br></br>
        <br></br>
        <br></br>

      </div>
    </div>)
}
