
import { IonHeader, IonLabel, IonSegment, IonSegmentButton, IonToolbar } from '@ionic/react';
import {useNavigate} from "react-router-dom"
import './NavBar.css'


export default function NavBar(props: {value: string}) {
  const navigate = useNavigate()

  const onClickHandler = (value: string) => {
    navigate(`/ntrejo/${value}`)
  }

  return (
    <IonHeader>
      <IonToolbar className='navBar'>
        <IonSegment value={props.value}>
          <IonSegmentButton value="home" onClick={() => onClickHandler("")}>
            <IonLabel>Home</IonLabel>
          </IonSegmentButton>
          <IonSegmentButton value="all-blogposts" onClick={() => onClickHandler("all-blogposts")}>
            <IonLabel>Blogposts</IonLabel>
          </IonSegmentButton>
          <IonSegmentButton value="contact" onClick={() => onClickHandler("contact")}>
            <IonLabel>Contact</IonLabel>
          </IonSegmentButton>
        </IonSegment>
      </IonToolbar>
    </IonHeader>
  );
}

