
import React, { FC, ReactElement } from 'react';
import { IonHeader, IonLabel, IonSegment, IonSegmentButton, IonTitle, IonToolbar } from '@ionic/react';
import './Image.css'

const CustomImage: FC<{source: string, imageNumber: number, caption: string, alt: string}> = ({source, imageNumber, caption, alt}): ReactElement => {
  return (
    <div className="container">
        <img alt={alt} src={source} />
        <p className='subtitle'>Image {imageNumber}: {caption}</p>
    </div>
  );
}

export default CustomImage;
