                <TextDisplayFull text={"All references to other work through the portfolio is identified and cited at the bottom. This is particularly true on the blogposts which are frequently inspired by another source. Just as citations were important, the portfolio also has evidence of growth throughout the semester. This is most apparent with the other major project: Stichonary. In that page, it describes the whole learning curve throughout and what must be changed. As that was my first major project, as that project grew I did as well. Particularly, one of the major takeaways was knowing the audience as well as how to work in a group and how that could impact design. Less noticable is the improved use of figma, the gap between the first parts of that project versus the end is significant though not easily noticible at first glance."} />

