
export enum BlogType {
    AllTypes,
    Freeform,
    ConceptMap,
    Tutorial,
    SketchInspiration,
    DirectedAnalysis,
    CompareContrast
}

export function blogTypeToString(type: BlogType) {
    switch (type) {
        case BlogType.Freeform:
            return "Free Form"
        case BlogType.ConceptMap:
            return "Concept Map"
        case BlogType.SketchInspiration:
            return "Sketch Inspiration"
        case BlogType.DirectedAnalysis:
            return "Directed Analysis"
        case BlogType.Tutorial:
            return "Tutorial"
        case BlogType.CompareContrast:
            return "Compare and Contrast"
        default:
            return "All Types"
    }
}

export type BlogpostDisplayDataProps = {
    title: string,
    source: string,
    type: BlogType,
    date: string,
    link: string,
    text: string,
}

