import NavBar from "../components/NavBar/NavBar";
import './post.css'
import { TextDisplayFull, TextDisplayImageLeft, TextDisplayImageRight } from "../components/TextDisplays/TextDisplay";
import init from './images/916-conceptmap.jpg'

export default function Sep15() {
    return (
        <div className="page">
            <NavBar value={""} />
            <div className='allBlogpostsPage'>
                <div className="allPostsTitleSection">
                    <br></br>
                    <h1 className='postTitle'>Color Theory in Christmas</h1>
                    <div className='postTypeDate'>
                        <h2>Concept Map</h2>
                        <h2>Sept 15, 2023</h2>
                    </div>
                    <br></br>
                </div>

                <br></br>
                <br></br>
                <div className="introOutro">
                    <TextDisplayImageRight text={"What I came up with was that, outside of relgion, Christmas is red and dark green with the ideas of family, joy, and warmth. The red brings in the idea of warmth and passion and hints at the idea of love being present. The darker greens are associated with hopefulness, wealth, and comfort. In terms of Christmas, this color offsets the red as complimentary colors as well as represents the idea of home. "} source={init} imageNumber={1} caption={"Concept map"} alt={"Concept map over colors and Christmas"} />
                </div>


                <br></br>
                <div className="citation-section">
                    <h3 className="Text-Headers"> Inspired By</h3>
                    <ul className="citationList">
                        <li className="citation">Color Harmony pg 26-55, 176-191</li>
                    </ul>
                </div>


                <br></br>
                <br></br>
            </div>
        </div>
    )
}
