import { IonAccordionGroup, IonAccordion, IonItem, IonLabel, IonTextarea, IonList, IonSelect, IonSelectOption, IonSearchbar, IonDatetimeButton, IonModal, IonDatetime } from "@ionic/react";
import './InputSection.css';
import { BlogType, blogTypeToString } from "../../data/BlogPosDisplayData";

export default function InputSection(props: { typeFunction: Function, searchFunction: Function, dateFunction: Function, type: string, search: string, month: string }) {
  return (
    <div className="inputSection">
      <IonList className="inputPart">
        <IonItem>
          <IonSelect onIonChange={(e: any) => props.typeFunction(e.target.value)} value={props.type} aria-label="Type" interface="popover" placeholder="Select type of post">
            {Object.keys(BlogType).filter((item) => {return isNaN(Number(item))}).map((key) => (
              <IonSelectOption key={key} value={key}>{blogTypeToString(BlogType[key as keyof typeof BlogType])}</IonSelectOption>
            ))}
          </IonSelect>
        </IonItem>
      </IonList>
      <IonSearchbar className="searchBar" onIonChange={(e: any) => props.searchFunction(e.target.value)} value={props.search} placeholder="Search"></IonSearchbar>
      <IonList className="inputPart">
        <IonItem>
          <IonSelect onIonChange={(e: any) => props.dateFunction(e.target.value)} value={props.month} aria-label="Type" interface="popover" placeholder="Select month of post">
            <IonSelectOption key={0} value={"all"}>All Months</IonSelectOption>
            <IonSelectOption key={9} value={"Sep"}>September</IonSelectOption>
            <IonSelectOption key={10} value={"Oct"}>October</IonSelectOption>
            <IonSelectOption key={11} value={"Nov"}>November</IonSelectOption>
          </IonSelect>
        </IonItem>
      </IonList>
    </div>
  )
}