import NavBar from "../components/NavBar/NavBar";
import { TextDisplayImageRight, TextDisplayFull, TextDisplayImageLeft } from "../components/TextDisplays/TextDisplay";
import { IonAccordionGroup, IonAccordion, IonItem } from '@ionic/react';
import init from './images/keyboardshortcuts.png'
import darkmode from './images/darkmode.png'
import highcontrast from './images/highcontrast.png'
import lightmode from './images/lightmode.png'
import zoomin from './images/zoomin.png'
import CustomImage from "../components/Image/Image";


export default function Oct23() {
    return (
        <div className="page">
            <NavBar value={""} />
            <div className='allBlogpostsPage'>
                <div className="allPostsTitleSection">
                    <br></br>
                    <h1 className='postTitle'>VSCode and Accessibility</h1>
                    <div className='postTypeDate'>
                        <h2>Directed Analysis</h2>
                        <h2>Oct 23, 2023</h2>
                    </div>
                    <br></br>
                </div>

                <br></br>
                <br></br>
                <div className="introOutro">
                    <TextDisplayFull text={"Within the reading, Krug states that accessible design is necessary as we can't expect none of our users to have an alternate requirement. Now, with that in mind, he states the best way to improve a site's accessibility for people that need alternate requirements is to improve it for everyone. In his own words, if someone has issues with a site and they don't need additional features, someone that does will still have difficulty following. "} />
                    <TextDisplayFull text={"In this blogpost we're going to take some of the relevant suggestions Krug makes and apply them onto Visual Studio Code to see how accessible it is."} />

                    <div className="postContent">
                        <IonAccordionGroup multiple={true}>
                            <IonAccordion value="first">
                                <IonItem slot="header" color="light">
                                    <h3 className="Text-Headers"> 1: Make All Content Keyboard Accessible </h3>
                                </IonItem>
                                <div className="ion-padding" slot="content">
                                    <TextDisplayImageLeft text={"1: Make all content accessible by keyboard. Now this is something that is possible in VSCode as well as is recommended as it speeds up programming. Below is a list of shortcuts that VSCode supports. 		"} source={init} imageNumber={1} caption={"VSCode Keyboard Actions"} alt={"Image of VSCode Keyboard Actions"} />              </div>
                            </IonAccordion>
                            <IonAccordion value="second">
                                <IonItem slot="header" color="light">
                                    <h3 className="Text-Headers"> 2: Create Contrast</h3>
                                </IonItem>
                                <div className="ion-padding" slot="content">
                                    <TextDisplayImageRight text={"2. Create significant contrast between text and background. This is something that VSCode does well as it has a dark, light, and high contrast mode, each pictured below. It also has the option to have a custom theme which changes the colors of the text and thus allowing more contrast as well as accessibility for color blindness."} source={darkmode} imageNumber={2} caption={"VSCode with dark mode"} alt={"VSCode with dark mode"} />
                                    <div className="side-image-box">
                                        <CustomImage source={highcontrast} imageNumber={3} caption={"VSCode with high contrast mode"} alt={"VSCode with high contrast mode"} />
                                        <CustomImage source={lightmode} imageNumber={4} caption={"VSCode with light mode"} alt={"VSCode with light mode"} />
                                    </div>
                                </div>
                            </IonAccordion>
                            <IonAccordion value="third">
                                <IonItem slot="header" color="light">
                                    <h3 className="Text-Headers"> 3: Increasing Text Size</h3>
                                </IonItem>
                                <div className="ion-padding" slot="content">
                                    <TextDisplayFull text={"3. Increasing text size increases other elements. I could not find a button to increase text size however, using Ctrl + I was able to zoom in and out. With it, the side bars and botto bars also grew in size, fufilling this requirement. Below is the result of a zoom in. "} />
                                    <div className="side-image-box">
                                        <CustomImage source={darkmode} imageNumber={5} caption={"VSCode in dark mode with default text size"} alt={"VSCode with default text size"} />
                                        <CustomImage source={zoomin} imageNumber={6} caption={"VSCode with increased size"} alt={"VSCode with increased size"} />
                                    </div>
                                </div>
                            </IonAccordion>
                        </IonAccordionGroup>
                    </div>
                    <TextDisplayFull text={"Overall, VSCode is fairly accessible though getting used to the structure as well as the keyboard shortcuts will take a bit for common users."} />
                </div>


                <div className="citation-section">
                    <h3 className="Text-Headers"> Inspired By</h3>
                    <ul className="citationList">
                        <li className="citation"> Don't Make Me Think Chapter 12
                        </li>
                    </ul>
                </div>


                <br></br>
                <br></br>
            </div>
        </div>)
}
