import NavBar from "../components/NavBar/NavBar";
import './post.css'
import { TextDisplayFull, TextDisplayImageLeft, TextDisplayImageRight } from "../components/TextDisplays/TextDisplay";
import init from './images/9-6-initialslide.png'
import uncluttered from './images/9-6-uncluttered.png'
import lessText from './images/9-6-lesstext.png'
import hierarchy from './images/9-6-texthierarchy.png'
import { IonAccordionGroup, IonAccordion, IonItem, IonLabel } from "@ionic/react";

export default function Sep18() {
  return (
    <div className="page">
      <NavBar value={""} />
      <div className='allBlogpostsPage'>
        <div className="allPostsTitleSection">
          <br></br>
          <h1 className='postTitle'>Designing Rules for Powerpoint</h1>
          <div className='postTypeDate'>
            <h2>Tutorial</h2>
            <h2>Sept 6, 2023</h2>
          </div>
          <br></br>
        </div>

        <br></br>
        <br></br>
        <div className="introOutro">
          <TextDisplayImageRight text={"Within this blogpost, we will be taking the slide below and making it more user friendly adapting the ideas for web design!"} source={init} imageNumber={1} caption={"Initial base slide"} alt={"Initial slide"} />
        </div>
        <div className="postContent">
          <IonAccordionGroup multiple={true}>
            <IonAccordion value="first">
              <IonItem slot="header" color="light">
                <h3 className="Text-Headers"> 1: Remove Distractions</h3>
              </IonItem>
              <div className="ion-padding" slot="content">
                <TextDisplayImageLeft text={"1. Remove distractions. \n One major issue with the slide is the amount of colors, images, and differing sizes. There's a lot going on this slide and it's difficult to tell what is important. Let's remove some of that and see what it would look cleaned up. \n \nHere is the same slide with less distractions."} source={uncluttered} imageNumber={2} caption={"Uncluttered slide"} alt={"Uncluttered Slide"} />              </div>
            </IonAccordion>
            <IonAccordion value="second">
              <IonItem slot="header" color="light">
                <h3 className="Text-Headers"> 2: Reduce Words</h3>
              </IonItem>
              <div className="ion-padding" slot="content">
                <TextDisplayImageRight text={"2. Remove unneccessary words \nThat looks better but see the amount of text on that slide Who is going to read that. Excess text makes the slide harder to read as well as less appealing. A common rule for powerpoints is the seven seven seven rule. Seven words in seven lines readable at seven feet away. Another approach recommended in the book, remove half of the words.\n\nHere is the same slide with the text reduced."} source={lessText} imageNumber={3} caption={"Slide with less text"} alt={"Slide with less text"} />              </div>
            </IonAccordion>
            <IonAccordion value="third">
              <IonItem slot="header" color="light">
                <h3 className="Text-Headers"> 3: Use Hierarchy</h3>
              </IonItem>
              <div className="ion-padding" slot="content">
                <TextDisplayImageLeft text={"3. Text hierarchy \n Now, all the text is the same size. While it looks okay, changing the font size and boldness relative to its importance. \n\nHere is the same slide with a text hierarchy"} source={hierarchy} imageNumber={4} caption={"Slide with hierarchy"} alt={"Slide with hierarchy"} />              </div>
            </IonAccordion>
          </IonAccordionGroup>
        </div>


        <br></br>
        <div className="citation-section">
          <h3 className="Text-Headers"> Inspired By</h3>
          <ul className="citationList">
            <li className="citation">Don't Make Me Think Chapter 2-5</li>
          </ul>
        </div>


        <br></br>
        <br></br>
      </div>
    </div>
  )
}
