import init from './images/10-1zoomondemand.png'
import NavBar from "../components/NavBar/NavBar";
import { TextDisplayImageRight, TextDisplayFull } from "../components/TextDisplays/TextDisplay";
export default function Oct1() {
  return (
    <div className="page">
      <NavBar value={""} />
      <div className='allBlogpostsPage'>
        <div className="allPostsTitleSection">
          <br></br>
          <h1 className='postTitle'>Deciding Design</h1>
          <div className='postTypeDate'>
            <h2>Free Form</h2>
            <h2>Oct 1, 2023</h2>
          </div>
          <br></br>
        </div>

        <br></br>
        <br></br>
        <div className="introOutro">
          <TextDisplayImageRight text={"The summary of this reading was honesty. Not saying rude things under the veneer of honesty, but truthfullness. No one goes into feedback wanting to get lied to, and trying to spare feelings might end up condescending instead. As it said, the idea of sandwhiching feedback has become cheap as it feels less genuine and more of a stepping stone to go to what matters. "} source={init} imageNumber={1} caption={"Handling content"} alt={"Image describing overview, selection, details"} />

          <TextDisplayFull text={"So how do we make if feel less cruel, keep it seperate. A big thing is remembering that your design isn't you and that a criticism to it isn't against you. Also, while honesty can be about negative things, it is also about the positives. Make sure to say what people are doing good, when appropriate, and narrow it down so they can do it again."} />

        </div>


        <br></br>
        <div className="citation-section">
          <h3 className="Text-Headers"> Inspired By</h3>
          <ul className="citationList">
            <li className="citation">Schneiderman and Info Organization PDF

            </li>
          </ul>
        </div>


        <br></br>
        <br></br>
      </div>
    </div>
  )
}
