
import { IonButton} from '@ionic/react';
import rW9EgY3xm_cropped from './rW9EgY3xm_cropped.png'
import { useNavigate } from 'react-router-dom';
import './NoPage.css'
export default function NoPage() {
    const navigate = useNavigate()

    return (
        <div className="underConstructionBox">
            <img className="reachToMoon" alt="astronaut in space" src={rW9EgY3xm_cropped} />
            <div className="redirectSection">
                <div className='redirectText'>
                    <h2>You've reached outerspace.</h2>
                    <h2>This page cannot be found.</h2>
                    <br></br>
                    <h2>Come back to base?</h2>
                </div>
                <IonButton className="backToBase" size='large' fill='outline' onClick={() => navigate('/ntrejo')}>Home</IonButton>

            </div>
        </div>
    );
}

