import NavBar from "../components/NavBar/NavBar";
import { TextDisplayFull, TextDisplayImageRight } from "../components/TextDisplays/TextDisplay";
import NoPage from "../views/NoPage/NoPage";
import init from './images/9-20-hierarchy.jpg'

export default function Sep20() {
    return (
        <div className="page">
            <NavBar value={""} />
            <div className='allBlogpostsPage'>
                <div className="allPostsTitleSection">
                    <br></br>
                    <h1 className='postTitle'>Resumes and Text Hierarchy</h1>
                    <div className='postTypeDate'>
                        <h2>Free Form</h2>
                        <h2>Sept 20, 2023</h2>
                    </div>
                    <br></br>
                </div>

                <br></br>
                <br></br>
                <div className="introOutro">
                    <TextDisplayImageRight text={"It's the season of resumes! And with that comes the constant redesigning and updating of previous resumes. The goal: getting people to get the information quickly, and that's where it ties in with today's readings."} source={init} imageNumber={1} caption={"Text Hierarchy Visual"} alt={"Text Hierarchy Visual"} />

                    <TextDisplayFull text={"Consistency is incredibly important in resumes, be that phrasing, structure, or text style. For this example today, we're focusing on text hierarchy. Text hierarchy is the structure of the text and relationships between one another. It frequently is made using different headings, sizes, to indicate importance as well as laying out more important items at the top of the paper. The reason I'm bringing this up is the readability aspect. Having a cl4ear hierarchy makes the resume easier to read as well as navigate, something incredibly important in the few seconds they look at it. So the next time you are looking to make changes, check the headings. See if the eye is flowing where you want it and that the headings match, are cohesive, and get gradually less important. "} />
                </div>


                <br></br>
                <div className="citation-section">
                    <h3 className="Text-Headers"> Inspired By</h3>
                    <ul className="citationList">
                        <li className="citation">https://www.designsystems.com/how-a-small-team-built-figma-dot-coms-design-system</li>
                        <li className="citation">https://www.designsystems.com/space-grids-and-layouts/</li>
                        <li className="citation">https://www.designsystems.com/typography-guides/</li>
                        <li className="citation">https://www.designsystems.com/guide-to-content-strategy-in-design-systems/</li>
                    </ul>
                </div>


                <br></br>
                <br></br>
            </div>
        </div>
    )
}
