
import { IonButton, IonHeader, IonLabel, IonSegment, IonSegmentButton, IonToolbar, useIonToast } from '@ionic/react';
import './AboutMe.css'
import { useNavigate } from 'react-router-dom';

export default function AboutMe() {
  const [present] = useIonToast();
  const navigate = useNavigate()

  const presentToast = (position: 'top' | 'middle' | 'bottom') => {
    present({
        buttons: [{ text: 'hide' }],
        message: "Resume Downloaded!",
        duration: 3000,
        position: position,
    });
}

  return (
    <div className='section'>
      <h1 className="name">Natalia Trejo</h1>
      <h2 className="caption">Junior Software Engineering Student at the Raikes School of Computer Science and Business at UNL with a focus on Front-End Development and Machine Learning. Graduating May 2025</h2>
      <br></br>
      <div className='buttons'>
        <a href="Natalia Trejo Resume.pdf" download="Natalia Trejo Resume" target='_blank'>
          <IonButton className = "aboutMeButtons" size='large' fill='outline' onClick={() => presentToast('top')}>Resume</IonButton>
        </a>
        <IonButton className = "aboutMeButtons" size='large' fill='outline' onClick={() => navigate('/ntrejo/contact')}>Contact</IonButton>
      </div>

    </div>
  );
}

