import init from './images/10-11Prototyping.png'
import NavBar from "../components/NavBar/NavBar";
import { TextDisplayImageRight, TextDisplayFull } from "../components/TextDisplays/TextDisplay";
export default function Oct11() {
    return (
        <div className="page">
            <NavBar value={""} />
            <div className='allBlogpostsPage'>
                <div className="allPostsTitleSection">
                    <br></br>
                    <h1 className='postTitle'>Prototyping</h1>
                    <div className='postTypeDate'>
                        <h2>Free Form</h2>
                        <h2>Oct 11, 2023</h2>
                    </div>
                    <br></br>
                </div>

                <br></br>
                <br></br>
                <div className="introOutro">
                    <TextDisplayImageRight text={"At this point, I've determined that Figma will be my defining skill by the end of the semester. Between the final project, design studio, and the redesign, I have spent a lot of hours creating components and adjusting design. I always thought that prototyping would be difficult, in IP I was in the Adobe camp, and that it would take up at least a week of time. Turns out, it is as simple as dragging the arrows across and setting an animation if necessary. It's a verb pleasent suprise about it and something I hope to use extensively when handling on click, on hover event. And while I have always seen figma as a static object, maybe with this all the designs can almost take a life of their own."} source={init} imageNumber={1} caption={"Figma Prototyping Visual"} alt={"Figma prototyping visual"} />

                </div>


                <br></br>
                <div className="citation-section">
                    <h3 className="Text-Headers"> Inspired By</h3>
                    <ul className="citationList">
                        <li className="citation"> https://www.youtube.com/watch?v=Saz6S1svYns


                        </li>
                    </ul>
                </div>


                <br></br>
                <br></br>
            </div>
        </div>    )
}
