import { IonAccordion, IonItem, IonLabel } from "@ionic/react";
import NavBar from "../../components/NavBar/NavBar"
import './Blogpost.css';
import InputSection from "./InputSection";
import { useEffect, useState } from "react";
import NameDisplay from "./NameDisplay";
import { mockPostDate } from "../../data/BlogPosDisplayData";
import { BlogType, BlogpostDisplayDataProps } from "../../data/BlogPosDisplayData";
import { postData } from "../blogposts/blogpostData";
import EndingSection from "../../components/EndingSection/EndingSection";

export default function Blogpost() {
    const [type, setType] = useState("AllTypes")
    const [search, setSearch] = useState("")
    const [date, setDate] = useState("")
    const [page, setPage] = useState(1)
    const [data, setData] = useState(postData)
    const [displayData, setDisplayData] = useState(data.slice(0, 10))

    useEffect(() => {
        let list = postData;

        if (type != BlogType[BlogType.AllTypes]) {
            list = list.filter(
                (post) => BlogType[post.type] === type
            )
        }

        if (date != "all") {
            list = list.filter(
                (post) => post.date.includes(date)
            )   
        }

        setDisplayData(list)
    }, [type, date])

    return (
        <div className="page">
            <NavBar value={"all-blogposts"} />
            <div className='allBlogpostsPage'>
                <div className="allPostsTitleSection">
                    <h1 className='allBlogpostsTitle'>All Blogposts</h1>
                    <br></br>
                    <InputSection type={type} search={search} month={date} typeFunction={setType} searchFunction={setSearch} dateFunction={setDate} />
                    <br></br>
                </div>
                <br></br>
                <br></br>
                <br></br>

                <NameDisplay data={displayData} />
                <br></br>
                <br></br>
                <br></br>
            </div>

            <EndingSection />

        </div>

    )
}
