import { useState } from "react";
import './ContactPage.css'
import { IonButton, IonTextarea, useIonToast } from '@ionic/react';
import emailjs from 'emailjs-com';

type NameProps = {
    title: string,
    setFunction: Function,
    value: string,
}

export default function Form() {
    const [message, setMessage] = useState("");
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [present] = useIonToast();


    const presentToast = (position: 'top' | 'middle' | 'bottom', message: string) => {
        present({
            buttons: [{ text: 'hide' }],
            message: message,
            duration: 3000,
            position: position,
        });
    }

    const clearInfo = () => {
        setMessage("")
        setName("")
        setEmail("")
    }

    const validateEmail = (email: string) => {
        const expression: RegExp = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;
        const result: boolean = expression.test(email); // true
        return result
    }

    const sendEmail = async () => {

        const templateParams = {
            to_name: 'Natalia Trejo',
            from_name: name,
            email: email,
            message: message
        };

        let serviceId = process.env.REACT_APP_EMAIL_SERVICE_ID as string
        let templateId = process.env.REACT_APP_EMAIL_TEMPLATE_ID as string
        let userId = process.env.REACT_APP_EMAIL_USER_ID as string

        emailjs.send(serviceId, templateId, templateParams, userId)
            .then((response) => {
                console.log('SUCCESS!', response.status, response.text);
            }, (err) => {
                console.log('FAILED...', err);
            });
    }


    const onSubmit = () => {
        if (message != "" && name != "" && email != "") {
            if (validateEmail(email)) {
                presentToast('top', "Email sent!")
                sendEmail()
                clearInfo()
            } else {
                presentToast('top', "Invalid Email.")
            }
        } else {
            presentToast('top', "Missing Form Sections.")
        }
    }

    function ContentSection() {
        return (
            <div className="contactContentSection">
                <h2>Content</h2>
                <IonTextarea value={message} className="contactContent" spellCheck={true} fill="solid" placeholder="Type message here" onIonChange={(e: any) => setMessage(e.target.value)}></IonTextarea>
            </div>
        )
    }

    function NameSection({ title, setFunction, value }: NameProps) {
        return (
            <div className="ahh">
                <h2>{title}</h2>
                <IonTextarea value={value} className="singleLineContactInfo" spellCheck={true} fill="solid" placeholder={`Your ${title}`} onIonChange={(e: any) => setFunction(e.target.value)}></IonTextarea>
            </div>
        )
    }

    return (
        <div className="contactForm">
            <ContentSection />
            <div className="contactNameEmail">
                <NameSection title="Name" setFunction={setName} value={name} />
                <NameSection title="Email" setFunction={setEmail} value={email} />
                <IonButton id="open-toast" className="contactSubmit" size='large' fill='outline' onClick={() => onSubmit()}>Submit</IonButton>
            </div>
        </div>
    )
}
