import { IonList, IonItem, IonLabel, IonThumbnail } from "@ionic/react";
import './NameDisplay.css';
import { BlogType, BlogpostDisplayDataProps, blogTypeToString } from "../../data/BlogPosDisplayData";
import { useNavigate } from "react-router-dom";

function ListItem(props: { title: string, type: BlogType, date: string, src: string, link: string }) {
    const navigate = useNavigate()


    // return (
    //     <div className="blogpostDisplayItem" onClick={() => navigate(props.link)}>
    //         <img alt="Silhouette of mountains" src={props.src} />
    //         <div className="blogpostWordSection">
    //             <h1 className="blogpostTitleCard">{props.title}</h1>
    //             <div className="blogpostCardCaption">
    //                 <h2>{blogTypeToString(props.type)}</h2>
    //                 <h2>{props.date}</h2>
    //             </div>
    //         </div>
    //     </div>
    // )

    return (
        <div className="blogpostDisplayItem" onClick={() => navigate(props.link)}>
            <div className="blogpostWordSection">
                <h1 className="blogpostTitleCard">{props.title}</h1>
                <div className="blogpostCardCaption">
                    <h2>{blogTypeToString(props.type)}</h2>
                    <h2>{props.date}</h2>
                </div>
            </div>
        </div>
    )
}

export default function NameDisplay(props: { data: BlogpostDisplayDataProps[]}) {
    return (
        <div className="blogpostList">
            {props.data.map((value) => (
                <ListItem title={value.title} type={value.type} date={value.date} src={value.source} link={value.link}/>
            ))}
            <br></br>
            <br></br>
        </div>
    )
}